import { httpGet, httpPost, getUrl, getQueryString } from '../common/js/http.js';


var qs = getQueryString();

if (qs['code']) {
	httpGet('/wechat/portal/oauth/accesstoken',
		{ code: qs['code'] },
		function (response) {
			alert(response)
			alert("token="+response.token)
			sessionStorage.Authorization = response.token;
			delete qs['code'];
			var url = location.hash.substr(1)
			if (url.indexOf('?') >= 0) {
				url = url.substring(0, url.indexOf('?'));
			}
			
			if(url.search(/index.html/i)){
				url = url.replace(new RegExp('index.html',"igm"), 'pay.html');
			}

			url = url + "?" + $.param(qs);
			alert(url);
			window.location.replace(url);
		},
		function (xhr, errorType, error) {
			if (xhr.status == 401) {
				alert("status:" + xhr.status + ", responseText:" + xhr.responseText + ", token:" + JSON.parse(xhr.responseText).token);
				sessionStorage.Authorization = JSON.parse(xhr.responseText).token;
				
				var url = getUrl("/index.html");
				window.location.replace(url);
			}
		});
}